<template>
    <div>
        <van-nav-bar
	        title="验证码"
            left-text="返回"
	        left-arrow 
          @click-left="$router.go(-1)"
	    />

        <van-form>
            <van-field
                v-model="email"
                name="邮箱"
                label="邮箱"
                placeholder="请输入邮箱"
            />
            <van-field
                v-model="verCode"
                type="text"
                name="验证码"
                label="验证码"
                placeholder="请输入邮箱验证码"
            >
                <template #button>
                <van-button size="small" @click="sendCode" :disabled="isSend">{{text}}</van-button>
                </template>
            </van-field>
            <div style="margin: 16px;">
                <van-button round block native-type="submit" @click="next">
                下一步
                </van-button>
            </div>
            <!-- <van-button type="default">默认按钮</van-button> -->
        </van-form>
    </div>
</template>

<script>
import ValidForm from '../assets/js/validForm'
    export default {
        data(){
            return {
                email:'',
                verCode:'',

                isSend:false,//验证码按钮禁用与否
                text:'发送验证码',
            }
        },
        methods:{

            //发送验证码
            sendCode(){
                //验证邮箱格式
                if(!ValidForm.validUserForm({email:this.email})){
                    return
                }

                //获取邮箱验证码
                let time = 30;
                this.text = time + '秒后重新发送';
                this.isSend = true;
                let timer = setInterval(() => {
                    if (time == 0) {
                    clearInterval(timer);
                    timer = null;
                    this.text = '发送验证码';
                    this.isSend = false;
                    } else {
                    time--;
                    this.text = time + '秒后重新发送';
                    }
                }, 1000);


                //获取邮箱验证码
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: "/emailValidCode",
                    data: {
                    appkey: this.appkey,
                    email: this.email
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                    this.$toast(result.data.msg);
                    
                    })
                    .catch(() => {
                        this.$toast.clear()
                    });
            },

            //跳转下一步 找回密码
            next() {

                if (!ValidForm.validUserForm({code:this.verCode})) {
                    return;
                }

                //发起发送验证码请求
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: "/checkValidCode",
                    data: {
                    appkey: this.appkey,
                    validCode: this.verCode
                    }
                })
                    .then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 'K001') {
                        this.$router.push({name:'backPassword'})
                    } else {
                        this.$toast(result.data.msg);
                    }
                    
                    })
                    .catch(() => {
                    this.$toast.clear();
                    
                    
                    });
            },

                
            
        }
    }
</script>

<style lang="less" scoped>
    @c:#f80;
    /deep/ .van-nav-bar__text {
    color: @c;
    }
    /deep/ .van-icon-arrow-left {
    color: @c;
    }
    //更改提交按钮颜色
    .van-button {
    background-color: @c;
    border: none;
    color: #fff;
    }
</style>